.wrap-401,
.wrap-404 {
  min-height: 500px;
  padding-top: 40px;
}
.wrap-401 .img-box,
.wrap-404 .img-box {
  height: 360px;
  max-width: 430px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  background-image: url('https://gw.alipayobjects.com/zos/rmsportal/wZcnGqRDyhPOEYFcZDnb.svg');
}
.wrap-401 .content,
.wrap-404 .content {
  padding-top: 40px;
}
.wrap-401 .content h1,
.wrap-404 .content h1 {
  color: #434e59;
  font-size: 72px;
  font-weight: 600;
  line-height: 72px;
  margin-bottom: 24px;
}
.wrap-401 .content .desc,
.wrap-404 .content .desc {
  color: rgba(0, 0, 0, 0.45);
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 16px;
}
.wrap-404 .img-box {
  height: 360px;
  max-width: 430px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  background-image: url("https://gw.alipayobjects.com/zos/rmsportal/KpnpchXsobRgLElEozzI.svg");
}



