.panel-group-container .content {
  background-color: #fff;
  text-align: center;
  color: #000;
  padding: 8px;
  height: 108px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1), 0 1px 4px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1), 0 1px 4px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  margin-bottom: 25px;
}
.panel-group-container .content:hover .icon-wrap i {
  color: #fff !important;
}
.panel-group-container .content:hover .icon-wrap.people {
  background-color: #bfa3a9;
}
.panel-group-container .content:hover .icon-wrap.money {
  background-color: #bf6883;
}
.panel-group-container .content:hover .icon-wrap.message {
  background-color: #5da0f0;
}
.panel-group-container .content:hover .icon-wrap.shoppingCard {
  background-color: #f6ab40;
}
.panel-group-container .content .icon-wrap {
  padding: 12px 14px;
  margin-left: -20px;
  -webkit-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  border-radius: 4px;
}
.panel-group-container .content .description p.text {
  margin-bottom: 12px;
  line-height: 18px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.45);
  font-weight: bold;
  text-align: left;
}
.panel-group-container .content .description span.num {
  display: block;
  font-weight: bold;
  text-align: left;
  font-size: 20px;
  color: #666;
}
.bgc-radius-shadow {
  padding: 8px;
  background-color: #fff!important;
}



