.login-container {
  background-color: #E9F0F5;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.login-container > .title {
  color: #eee;
  font-size: 26px;
  font-weight: 400;
  margin: 0px auto 30px auto;
  text-align: center;
  font-weight: bold;
  letter-spacing: 1px;
}
.login-container .spin-wrap {
  text-align: center;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  line-height: 90vh;
  z-index: 999;
}
.login-container .content {
  position: absolute;
  background-color: #fff;
  left: 50%;
  top: 50%;
  width: 320px;
  padding: 30px 30px 0 30px;
  -webkit-transform: translate(-50%, -60%);
      -ms-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%);
  -webkit-box-shadow: 0 0 10px 2px rgba(40, 138, 204, 0.16);
          box-shadow: 0 0 10px 2px rgba(40, 138, 204, 0.16);
  border-radius: 3px;
}
.login-container .content .two-button-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.login-container .content .row-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.login-container .content .user-type-button {
  width: 45%;
}
.login-container .content .login-form-button {
  width: 100%;
}
.login-container .content .login-form-forgot {
  float: right;
}



